import { http } from '@/http';
import store from '@/store';
import Toast from "vant/es/toast";
import "vant/es/toast/style/less";
import { computed, defineComponent, onMounted, reactive, toRefs } from 'vue';
export default defineComponent({
    name: 'SellerLayout',
    setup() {
        const state = reactive({
            includeRoutes: ['SellerActs', 'SellerFans', 'SellerMessage', 'SellerInfo']
        });
        const icon1 = {
            active: 'https://qrmkt.oss-cn-beijing.aliyuncs.com/hbseller_client/tb-1-1.png',
            inactive: 'https://qrmkt.oss-cn-beijing.aliyuncs.com/hbseller_client/tb-1-0.png'
        };
        const icon2 = {
            active: 'https://qrmkt.oss-cn-beijing.aliyuncs.com/hbseller_client/tb-2-1.png',
            inactive: 'https://qrmkt.oss-cn-beijing.aliyuncs.com/hbseller_client/tb-2-0.png'
        };
        const icon3 = {
            active: 'https://qrmkt.oss-cn-beijing.aliyuncs.com/hbseller_client/tb-3-1.png',
            inactive: 'https://qrmkt.oss-cn-beijing.aliyuncs.com/hbseller_client/tb-3-0.png'
        };
        const icon4 = {
            active: 'https://qrmkt.oss-cn-beijing.aliyuncs.com/hbseller_client/tb-4-1.png',
            inactive: 'https://qrmkt.oss-cn-beijing.aliyuncs.com/hbseller_client/tb-4-0.png'
        };
        const hasMsg = computed(() => store.state.hasMsg);
        onMounted(() => {
            if (hasMsg.value === null) {
                http
                    .get('/hbSeller/main/role', {})
                    .then((res) => {
                    if (res.code === '200') {
                        if (res.data.hasMsg) {
                            store.commit('setHasMsg', true);
                        }
                        else {
                            store.commit('setHasMsg', false);
                        }
                    }
                    else {
                        Toast.fail(res.msg);
                    }
                })
                    .catch((err) => {
                    console.log(err);
                });
            }
        });
        return {
            ...toRefs(state),
            icon1,
            icon2,
            icon3,
            icon4,
            hasMsg
        };
    }
});
